* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  margin: 0;
  overflow-x: hidden;
  font-family: Arial, sans-serif;
  height: 300vh;
  background: linear-gradient(to bottom, #001f3f, #0074d9);
}

.main {
  position: fixed;
  width: 100%;
  top: 0;
  left: 0;
  width: 100%;
  height: 100vh;
}

.tree_image {
  position: absolute;
  width: 100%;
  height: 100vh;
  pointer-events: none;
}

.card {
  position: fixed;
  width: 40%;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  text-align: center;
  color: white;
  padding: 20px;
  background-color: rgba(0, 0, 0, 0.164);
  border-radius: 30px;
  z-index: 0;
}

.card:not(.front) {
  opacity: 0;
}

.title {
  margin-bottom: 20px;
}

.skills-container {
  display: flex;
  justify-content: center;
  gap: 20px;
}

.gif {
  filter: invert();
}

a {
  text-decoration: none;
  color: rgb(32, 129, 84);
}

.video {
  transform: translate(-30px);
}

.sIcon {
  width: 20px;
  height: 20px;
}

.snowflake {
  position: absolute;
  z-index: 5;
  top: -10%;
  animation: fall var(--flake-duration) linear infinite;
  width: var(--flake-size);
  left: var(--flake-position-x);
  will-change: transform, opacity; /* Hint to the browser */
}


@keyframes fall {
  0% {
    transform: translateY(0);
    opacity: 1;
  }

  100% {
    transform: translateY(110vh);
    opacity: 0;
  }
}


.tree_image.front {
  filter: saturate(1) brightness(1);
  z-index: 3;
}

.tree_image.middle {
  filter: saturate(0.8) brightness(1.3);
  z-index: 2;
}

.tree_image.back {
  filter: saturate(0.6) brightness(1.6);
  z-index: 1;
}

.card.front {
  z-index: 2;
}

.card.middle {
  z-index: 1;
}

.card.back {
  z-index: 0;
}